.accordion-wrapper {
  padding-bottom: 50px;
  .accordion {
    color: $color-dark-blue;

    &__panel {
      border-bottom: 1px solid rgb(236, 236, 236);
      padding: 25px 0;
    }

    &__content {
      display: grid;
      grid-template-rows: 0fr;
      transition: grid-template-rows 500ms;

      &[aria-hidden='false'] {
        grid-template-rows: 1fr;
      }
    }

    &__trigger {
      background: transparent;
      padding: 0;
      border: 0;
      font: inherit;
      color: inherit;
      font-size: $small-font-size;
      text-align: left;
      cursor: pointer;

      &:hover {
        color: $color-light-red;
      }

      @media (min-width: 750px) {
        font-size: 17px;
      }
    }

    &__answer-container {
      overflow: hidden;
      grid-row: 1 / span 2;
    }

    &__answer {
      padding-top: 20px;
      font-size: $small-font-size;
      line-height: 30px;
      color: $color-grayish;
      text-align: justify;
    }
  }
}
